body {
  margin: 0px;
  padding: 0px;
  height: auto;
  /* background-color: #F6F8FA; */

  /* SAILOR MOON */
  /* cursor: url(http://www.rw-designer.com/cursor-extern.php?id=99842), crosshair; */

  /* REAL MOON */
  cursor: url(http://www.rw-designer.com/cursor-extern.php?id=59754), crosshair;

  /* MAPLESTORY */
  /* cursor: url(http://www.rw-designer.com/cursor-extern.php?id=35038), crosshair; */
}

body.homeBackground {
  background-image: url(https://media.giphy.com/media/F3LuIoVXgKAxHS3zFv/giphy.gif);
  background-repeat: no-repeat;
  background-size: cover;
}

h1 {
  font-family: Verdana-Bold;
  font-size: 16px;
  color: #22262a;
  text-align: left;
  margin: 0px 0px;
}

strong {
  font-family: Verdana;
  font-weight: 700;
  font-size: 12px;
  color: #111315;
  text-align: left;
}

p {
  font-family: Verdana;
  font-size: 12px;
  color: #111315;
  text-align: left;
  margin: 0px 0px;
}

a {
  font-family: Verdana;
  font-weight: 700;
  font-size: 12px;
  color: #013299;
  text-align: left;
  text-decoration: none;
}

a:hover {
  cursor: pointer;
  color: #cc0000;
  text-decoration: underline;
}

a.navLinkText:hover {
  font-weight: 700;
  color: white;
  text-decoration: underline;
}

.redNumber {
  font-family: Verdana;
  font-size: 14px;
  color: #cc0000;
}

.strongRedGroup {
  display: flex;
  align-items: flex-end;
  margin-top: 0px;
  margin-bottom: 8px;
}

/* In JSX we can create linebreaks with whitespace now. No more divs! */
.linebreak {
  white-space: pre-line;
}

.orangeContentContainer {
  margin-bottom: 20px;
}
#navigation {
    width: 900px;
    height: 32px;
    /* background-color: #6698CA; */
    background-image: -webkit-gradient(linear, left top, left bottom, from(#1C4996), to(#183C7D));
    background-image: -webkit-linear-gradient(#1C4996, #183C7D);
    background-image: linear-gradient(#1C4996, #183C7D);
    position: static;
    padding: 0px;
    margin: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: auto;
}

.navLinkGroup {
    width: 860px;
    display: flex;
    position: absolute;
    align-items: center;
    /* margin-left: 20px; */
    overflow: auto;
}

.navLinkText {
    font-family: Verdana;
    font-size: 12px;
    color: #FFFFFF;
    text-align: left;
    padding: 0px 8px;
    text-decoration: none;
    font-weight: 400;
}

.navLinkText:first-child {
    padding-left: 0px;
}

.navLinkText:hover {
    font-weight: 700;
    cursor: pointer;
}

#navDivider {
    font-family: Arial;
    font-weight: 700;
    font-size: 12px;
    color: #557A9E;
    text-align: left;
}
.contactContainer {
    border: 3px solid #6699CB;
    margin-bottom: 20px;
}

.blueHeader {
    font-family: Verdana;
    font-weight: 700;
    font-size: 12px;
    color: #FFFFFF;
    text-align: left;
    background-color: #6699CB;
    height: 23px;
    padding-left: 8px;
    border: none;
    line-height: 23px;
}

#contactImages {
    margin: 4px 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

#contactImages:hover {
    cursor: pointer;
}
.blueContentBlock {
    margin: 4px 4px;
    display: grid;
    grid-template-columns: 1fr 1.7fr;
    grid-column-gap: 4px;
    -webkit-column-gap: 4px;
       -moz-column-gap: 4px;
            column-gap: 4px;
}

p.blueContentHeader {
    background-color: #B1CFF0;
    font-family: Verdana;
    font-weight: 700;
    font-size: 12px;
    color: #336699;
    padding-left: 4px;
    padding-top: 4px;
    text-align: left;
}

p.blueContentDetails {
    background-color: #D5E8FB;
    font-family: Verdana;
    font-size: 12px;
    color: #22262A;
    text-align: left;
    padding: 4px 0px 4px 4px;
}
.blueEducationBlock {
    margin: 4px 4px;
    display: grid;
    grid-template-columns: 3.66fr 1fr;
    grid-column-gap: 4px;
    -webkit-column-gap: 4px;
       -moz-column-gap: 4px;
            column-gap: 4px;

}

.blueEducationHeader {
    background-color: #B1CFF0;
    padding: 0px 0px 20px 4px
}

#blueEducationSchool {
    padding-top: 4px;
    font-family: Verdana;
    font-weight: 700;
    font-size: 12px;
    color: #013299;
    text-align: left;
}

#blueEducationSchool:hover {
    color: #cc0000;
    text-decoration: underline;
}
.projectCard {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projectTitle {
    color: #013299;
    margin-bottom: 2px;
}

.projectCard:hover {
    cursor: pointer;

}

.projectCard:hover .projectTitle {
    color: #CC0000;
    text-decoration: underline;
}
.homeContent {
    width: 900px;
    height: auto;
    margin: auto;
    background-color: white;
}

.contentGrid {
    padding: 20px 20px;
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    grid-column-gap: 32px;
    -webkit-column-gap: 32px;
       -moz-column-gap: 32px;
            column-gap: 32px;

}

.contentColumn {
    display: flex;
    flex-direction: column;
}


#extendedNetwork {
    border-style: solid;
    border-width: 3px;
    border-color: #22262A;
    padding: 16px;
    height: auto;
    text-align: center;
    margin-bottom: 20px;
}
